<template>
  <div class="box">
    <el-container>
      <el-aside width="200px">
        <el-tree
          :data="projectList"
          :props="projectProps"
          @node-click="handleNodeClick"
          highlight-current
        >
          <template #default="{ data }">
            <component class="icons" :is="data.icon"></component>
            <span style="margin-left: 10px">{{ data.projectName }}</span>
          </template>
        </el-tree>
      </el-aside>
      <el-main>
        <div v-if="currentProject == null" style="font-size: 18px">
          请选择项目
        </div>
        <div v-else>
          <el-tabs type="border-card" class="demo-tabs">
            <!-- Tree View部分 -->
            <el-tab-pane>
              <!-- 标题部分 -->
              <template #label>
                <span class="custom-tabs-label">
                  <el-icon><Link /></el-icon>
                  <span>Tree View</span>
                </span>
              </template>
              <!-- 文件树 -->
              <el-tree
                :data="currentProject.childProjects"
                :props="childProjectProps"
                @node-click="childhandleNodeClick"
                highlight-current
              >
                <template #default="{ data }">
                  <component class="icons" :is="data.icon"></component>
                  <span style="margin-left: 10px">{{ data.projectName }}</span>
                </template>
              </el-tree>
            </el-tab-pane>
            <!-- Grapha View部分 -->
            <el-tab-pane>
              <!-- 标题部分 -->
              <template #label>
                <span class="custom-tabs-label">
                  <el-icon><Link /></el-icon>
                  <span>Grapha View</span>
                </span>
              </template>
              <div v-if="currentProject.childProjects.length > 0">
                <!-- 图形展示部分 -->
                <div class="graph">
                  <div class="circle1" style="margin-right: 0px">
                    <el-button
                      @click="drawer = true"
                      style="margin: auto"
                      class="drawer-btn"
                    >
                      File1
                    </el-button>
                  </div>
                  <div style="margin: auto 0">-------------------></div>
                  <div
                    class="circle1"
                    style="background-color: red; margin: auto 0px"
                  >
                    <el-button
                      @click="drawer = true"
                      style="margin: auto; background-color: red"
                      class="drawer-btn"
                    >
                      CAD-document
                    </el-button>
                  </div>
                  <div style="margin: auto 0">-------------------></div>
                  <div
                    class="circle1"
                    style="background-color: green; margin-left: 0px"
                  >
                    <el-button
                      @click="drawer = true"
                      style="margin: auto; background-color: green"
                      class="drawer-btn"
                    >
                      MySQL-document
                    </el-button>
                  </div>
                </div>
                <!-- 弹出详情部分 -->
                <el-drawer
                  title="我是标题"
                  v-model="drawer"
                  :with-header="false"
                >
                  <el-descriptions
                    title="Descriptions"
                    :column="1"
                    border
                    style="font-style: normal"
                  >
                    <el-descriptions-item
                      label="Property"
                      :labelStyle="{
                        width: '25px',
                        'font-weight': 'bold',
                        'font-size': '18px',
                      }"
                      :contentStyle="{
                        'font-weight': 'bold',
                        'font-size': '18px',
                      }"
                    >
                      Value
                    </el-descriptions-item>
                    <el-descriptions-item label="project_id"
                      >项目id</el-descriptions-item
                    >
                    <el-descriptions-item label="project_name"
                      >项目名字</el-descriptions-item
                    >
                    <el-descriptions-item label="project_descriptions"
                      >项目描述</el-descriptions-item
                    >
                    <el-descriptions-item label="project_creattime"
                      >项目创建时间</el-descriptions-item
                    >
                    <el-descriptions-item label="model_id"
                      >模型id</el-descriptions-item
                    >
                    <el-descriptions-item label="model_source"
                      >模型源头</el-descriptions-item
                    >
                    <el-descriptions-item label="moudle_target"
                      >模型目标</el-descriptions-item
                    >
                    <el-descriptions-item label="model_data"
                      >模型数据类型</el-descriptions-item
                    >
                    <el-descriptions-item label="model_url"
                      >外部模型数据地址</el-descriptions-item
                    >
                    <el-descriptions-item label="model_version"
                      >模型版本</el-descriptions-item
                    >
                    <el-descriptions-item label="model_modeified"
                      >模型修改时间</el-descriptions-item
                    >
                  </el-descriptions>
                </el-drawer>
              </div>
              <div v-else>暂无数据</div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script setup>
import { ref, getCurrentInstance, onMounted } from "vue";
const { proxy } = getCurrentInstance();

// 左侧 el-tree配置
const projectProps = {
  // 目前只有一级目录，没有子目录，所以children不需要启用
  // children: "children",
  label: "projectName",
};
// 左侧 el-tree 点击事件 点击对应项目后，currentProject为当前点击项目
let currentProject = ref(null);
const handleNodeClick = (data) => {
  currentProject.value = data;
  console.log(currentProject.value.childProjects.length);
};
// 左侧 el-tree 通过本地api获取项目
let projectList = ref([]);
const getRepoList = async () => {
  let tempList = await proxy.$api.getRepoList();
  projectList.value = tempList.list;
  // console.log("得到项目的信息数据", projectList);
};

const activeName = ref("first");

// 右侧 Tree View 设置
// 右侧 el-tree配置
const childProjectProps = {
  // 目前只有一级目录，没有子目录，所以children不需要启用
  children: "childProjects",
  label: "projectName",
};
// 右侧 el-tree 点击事件 点击对应项目后，childCurrentProject为当前点击项目
let childCurrentProject = ref(null);
const childhandleNodeClick = (data) => {
  // console.log("handleNodeClick", data);
  childCurrentProject.value = data;
  // console.log("childCurrentProject", childCurrentProject.value);
};

// 右侧 Graph View 设置
// drawer控制详情框是否弹出，false不弹出，true弹出
let drawer = ref(false);
onMounted(() => {
  // 该组件被启用时就要调用获取项目总数函数
  getRepoList();
});
</script>

<style lang="less" scoped>
.box {
  display: flex;
  flex-flow: column;
  height: 100%;
}
.el-aside {
  height: 100%;
  border-right: rgb(226, 226, 226) 5px solid;
}
.el-tree {
  padding: 5px;
  font-size: 15px;
}
.el-main {
  padding: 0px;
}
.el-tabs {
  font-style: italic;
}

.demo-tabs > .el-tabs__content {
  padding: 32px;
  color: #6b778c;
  font-size: 32px;
  font-weight: 600;
}
.demo-tabs .custom-tabs-label .el-icon {
  vertical-align: middle;
}
.demo-tabs .custom-tabs-label span {
  vertical-align: middle;
  margin-left: 4px;
}
.el-tabs--border-card {
  border: 0px;
}
.icons {
  width: 18px;
  height: 18px;
}

.graph {
  height: 500px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.circle1 {
  /*background: burlywood;*/
  background-color: #fff;
  border: 2px solid #000;
  color: #000;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: auto;
  text-align: center; /* 文字水平居中 */
  line-height: 150px;
}
.drawer-btn {
  background-color: #fff;
  border: 0;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  color: #000;
}
</style>